  | 'neutral-contrast-low'
  | 'notification-success'
  | 'notification-success-soft'
  | 'notification-warning'
  | 'notification-warning-soft'
  | 'notification-error'
  | 'notification-error-soft'
  | 'notification-neutral'
  | 'notification-neutral-soft'
  | 'state-hover'
  | 'state-active'
  | 'state-focus'
  | 'state-disabled'
  | 'external-facebook'
  | 'external-google'
  | 'external-instagram'
  | 'external-linkedin'
  | 'external-pinterest'
  | 'external-twitter'
  | 'external-wechat'
  | 'external-whatsapp'
  | 'external-xing'
  | 'external-youtube';

